
import { Breadcrumb } from 'antd'
import { memo } from 'react'
import { Link } from 'react-router-dom'

 function CustomBreadcrumb(props) {
  let list = props.arr.map(item=>({title:item}))
  list.unshift({
    title:<Link to="/index">首页</Link>
  })
  console.log(list)
  return (
    <Breadcrumb items={list} style={{ marginBottom: 16 }}>

    </Breadcrumb>
  )
}
export default memo(CustomBreadcrumb)